import React, { useEffect } from 'react'
import { MapContainer, GeoJSON, TileLayer, ImageOverlay, ScaleControl, useMap, Circle, Marker, Popup, Tooltip } from "react-leaflet";
import * as L from "leaflet";
import { BaseMapsLayers, mapCenter, maxBounds, setDragging } from '../helpers/mapFunction'
import "leaflet/dist/leaflet.css";
import "leaflet-fullscreen/dist/Leaflet.fullscreen.js";
import "leaflet-fullscreen/dist/leaflet.fullscreen.css";
import MapLegend from './MapLegend';
import FiltererdJsonData from './FiltererdJsonData';
import MonsoonData from "../assets/data/MonsoonData.json"
import monsoon_anomaly_legend from "../assets/images/monsoon_anomaly_legend.jpg"
import marker_icon from "../assets/images/marker_icon.png"
import IndiaStates from '../assets/data/shapefiles/IndiaStates.json';

const DecesionSupportMap = ({ selectedState, selectedDistrict, selectedMapData, selectedTehsil, filteredIndiaDistrict, selectedVariable, uploadeddata, mapNumber }) => {


  const isGeoJSON = uploadeddata && uploadeddata.type === "FeatureCollection";

  const renderMarkers = () => {
    return uploadeddata.map((item, index) => (
      <Marker
        key={index}
        position={[parseFloat(item.Latitude), parseFloat(item.Longitude)]}
        icon={L.icon({
          iconUrl: marker_icon,
          iconRetinaUrl: marker_icon,
          iconSize: [15],
          popupAnchor: [0, -5],
        })}
      >
        <Tooltip>{`ID: ${item.ID}`}</Tooltip>
      </Marker>
    ));
  };

  const renderGeoJSON = () => {
    return (
      <GeoJSON
        data={uploadeddata}
        style={{ fillColor: 'none', weight: 3, color: 'yellow', fillOpacity: "0.4" }}
      />
    );
  };


  function TalukaOnEachfeature(feature, layer) {
    layer.on('mouseover', function () {
      const DataItem = MonsoonData.find(item => item.ID === feature.properties.ID);

      if (feature.properties && feature.properties.ID) {
        let popupContent;
        const value = DataItem[selectedVariable.value] ? DataItem[selectedVariable.value].toFixed(2) : "NA"

        popupContent = `
            <div>
            ID: ${feature.properties.ID}<br/>
            STATE: ${feature.properties.STATE}<br/>
            DISTRICT: ${feature.properties.DISTRICT}<br/>
            TEHSIL: ${feature.properties.TEHSIL}<br/>
            VALUE: ${value}
                
               
                
            </div>
    `;

        layer.bindTooltip(popupContent, { sticky: true });
      }
      layer.openTooltip();
    });

    layer.on('mouseout', function () {
      layer.closeTooltip();
    });
  }



  const TalukaDensity = (density => {
    return density > 30 ? '#053062'
      : density > 20 ? '#2F7AB6'
        : density > 10 ? '#87BEDA'
          : density > 0 ? '#DDEBF2'
            : density > -10 ? '#FBE3D6'
              : density > -20 ? '#F09C7A'
                : density > -30 ? '#C13739'
                  : '#68001F';
  })



  const TalukaStyle = (feature => {

    const getDensityFromSummerData = (ID) => {
      const DataItem = MonsoonData.find(item => item.ID === ID);
      return DataItem ? DataItem[selectedVariable.value] : null;
    };

    const density = getDensityFromSummerData(feature.properties.ID);

    return ({
      fillColor: TalukaDensity(density),
      weight: 0.5,
      opacity: 1,
      color: 'black',
      // dashArray: '2',
      fillOpacity: 1
    });
  });

  const setInitialMapZoom = () => {
    var viewportWidth = window.innerWidth;
    var mapZoom;
    if (viewportWidth <= [767]) {
      mapZoom = [3];
    } if (viewportWidth >= [768]) {
      mapZoom = [3.3];
    } if (viewportWidth >= [1600]) {
      mapZoom = [4];
    }
    return mapZoom;
  }


  return (
    <>
      <MapContainer
        // fullscreenControl={true}
        center={mapCenter}
        style={{ width: '100%', height: "100%", backgroundColor: 'white', border: 'none', margin: 'auto' }}
        zoom={setInitialMapZoom()}
        maxBounds={maxBounds}
        // maxZoom={8}
        minZoom={setInitialMapZoom()}
        keyboard={false}
        dragging={setDragging()}
        attributionControl={false}
        // scrollWheelZoom={false}
        doubleClickZoom={false}
      >

        {filteredIndiaDistrict && selectedState && selectedVariable ? (
          <>
            <div className='decision_map_heading'>
              <p> {selectedVariable.name} </p>
            </div>

            <div className="legend_panel_container" style={{ width: "270px", bottom: "5px", right: "5px", backgroundColor:"white", borderRadius:"5px"}}>
              <div className="item-heading" >
                <p style={{ fontSize: "12px" }}>Changes in last decade (2012-2022) compared to climate baseline (1982-2011) (in %)</p>
              </div>
              <img style={{ width: "100%" }} src={monsoon_anomaly_legend} alt='Legend' />
            </div>
            <FiltererdJsonData
              filteredDataStyle={TalukaStyle}
              TalukaOnEachfeature={TalukaOnEachfeature}
              selectedState={selectedState}
              selectedDistrict={selectedDistrict}
              selectedTehsil={selectedTehsil}
              filteredIndiaDistrict={filteredIndiaDistrict}
            />


          </>
        ) : (
          <>
            <div className='decision_map_heading' style={{ width: "100px", right: "10px" }}>
              <p> {mapNumber} </p>
            </div>

            <GeoJSON
              key={selectedState + selectedDistrict + selectedTehsil}
              style={{
                fillColor: "black",
                weight: 1,
                color: "black",
                fillOpacity: "0.001",
                interactive: false,
              }}
              data={IndiaStates.features}
            />
          </>

        )}

        {isGeoJSON ? renderGeoJSON() : renderMarkers()}





        <ScaleControl />

      </MapContainer>
    </>
  )
}

export default DecesionSupportMap